import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import * as Api from "../../api/api";
import remindertable from "./remindertable";
import moment from "moment";

@Component({
  props: {},
  components: {
    remindertable
  }
})
export default class AddGoods extends Vue {
  handleOrderStatus(status) {
    switch (status) {
      case "ORDER_INIT":
        return "订单初始化";
      case "ORDER_WAIT_PAY":
        return "等待支付";
      case "ORDER_PAY_ONDEV":
        return "货到付款";
      case "ORDER_CANCEL_PAY":
        return "交易关闭";
      case "ORDER_WAIT_SENDGOODS":
        return "待发货";
          case "SENDGOODS_UNFINISHED":
        return "发货未完成";
      case "ORDER_WAIT_RECVGOODS":
        return "待收货";
      case "ORDER_END_GOODS":
        return "订单已完成";
      default:
        return "";
    }
  }
paymentMode =''
  reminderVEnums = "PENDING";
date=[]
  allprescription = 0;
  // allPrescription() {
  //   indexApi.allPrescription().then(res => {
  //     if (res["retCode"]) {
  //       console.log(res.data);

  //     } else {
  //       if(!res['islogin']){this.$alert(res["message"]);}
  //       console.error("数据查询错误");
  //     }
  //   });
  // }
  loading = false;
  getOrderExcel() {
    this.loading = true;   
    
    indexApi.getExcelUrl({
    startCreateDate:this.date && this.date.length>0? moment(this.date[0]).format("YYYY-MM-DD") + " 00:00:00":"",
        endCreateDate:this.date && this.date.length>0? moment(this.date[1]).format("YYYY-MM-DD") + " 23:59:59":"",
        orderStatusEnum: this.rderStatus,
                paymentMode:this.paymentMode
    }).then(res => {
      if (res["retCode"]) {

        console.log(res.data)

        if (res.data.num>0) {
          this.doResult(res.data.durl);
        } else {
        this.loading = false;
          this.$alert("查询结果没有可导出数据");
        }
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
      }
    });
  }
durl =''
doDownLoad(){
  window.open(this.durl);
}
  doResult(durl) {

    indexApi.orderDoResult({}).then(res => {
        this.loading = false;
      if (res["retCode"]) {
        this.$message("导出成功");
          this.durl = durl
          // sessionStorage
          // 塞到缓存
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
      }
    });
  }

  key = "";
  page = 0;
  pageSize = 10;
  total = 100;
  onPageChange(page) {
    this.page = page - 1;
    this.getOrderList();
  }
  orderList = [];

  consigneeAddress = "";
  handleClick(e) {
    this.rderStatus = "";
    this.key= "";
this.paymentMode = "";
this.consigneeAddress ="";
this.date=["",""];
this.rderStatus ="";
 this.page = 0
    this.getOrderList();
  }
  rderStatus = "";
  getOrderList(filter=null) {
    if(filter){
      this.page = 0
    }
    this.reminderVEnums == "ALL";
    this.loading = true
    indexApi
      .gerOrderList({
        paymentMode:this.paymentMode,
        reminderVEnums: this.reminderVEnums,
        key: this.key,
        consigneeAddress: this.consigneeAddress,
        startCreateDate: this.date && this.date.length>0? moment(this.date[0]).format("YYYY-MM-DD") + " 00:00:00":"",
        endCreateDate: this.date && this.date.length>0? moment(this.date[1]).format("YYYY-MM-DD") + " 23:59:59":"",
        orderStatusEnum: this.rderStatus,
        page: this.page,
        pageSize: this.pageSize
      })
      .then(res => {
    this.loading = false
        this.queryOrderCount();
        if (res["retCode"]) {
   
          this.orderList = res.data.data;
          this.total = res.data.page.total;
     
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
  }

  PENDING = 0;
  queryOrderCount() {
    indexApi.queryOrderCount({}).then(res => {
      if (res["retCode"]) {
        this.PENDING = res.data.PENDING;
        this.allprescription = res.data.all;
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }

      }
    });
  }

  mounted() {

    // this.allPrescription();
    this.getOrderList();

  }
}



